.App {
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: teal;
  padding: 2px;
}

.result input{
  width: 100%;
  padding: 5px;
  background-color: #fff;
  color: #000;
  font-size: 16px;
  font-weight: 800;
  text-align: right;
  border: none;
}

.user-input input {
  width: 100%;
  padding: 5px;
  background-color: #fff;
  color: #000;
  font-size: 15px;
  font-weight: 700;
  border: none;
}

.my-button{
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
}

.my-button button {
  /* width: 100px;*/
  height: 50px;
  flex-basis: 25%;
  font-size: 16px;
  font-weight: bolder;
}

@media (max-width:576px){
  .App {
    width: 100vw;
    background-color: #999;
  }
}
